import React, { useState } from 'react';
import './style.scss';
import icon from './img/icon.svg';

const questionData = [
  { number: '01', title: '    ?', answer: " -            25$   ,        ,    . "},
  { number: '02', title: '    ?', answer: "        - 2-3 .   - -  1 . "},
  { number: '03', title: '    / ,     ?', answer: "        . ,             ,   ,     . "},
  { number: '04', title: '      (     )?', answer: "         .  ,         ,        - ,  !        . "},
  { number: '05', title: '     ?', answer: ",          ,      .       , ,        ."}
];

function FAQs() {
  const [expandedQuestions, setExpandedQuestions] = useState([]);

  const toggleQuestion = (index) => {
    if (expandedQuestions.includes(index)) {
      setExpandedQuestions(expandedQuestions.filter((item) => item !== index));
    } else {
      setExpandedQuestions([...expandedQuestions, index]);
    }
  };

  return (
      <section className="question" id="FAQs">
        <div className="question-container">
          <h2 className="question-container--title"> </h2>
          <div className="question-container--scrollable">
            {questionData.map((question, index) => (
                <div
                    key={question.number}
                    className={`question-container--list ${
                        expandedQuestions.includes(index) ? 'expanded' : ''
                    }`}
                >
                  <p className="question-container--number">{question.number}</p>
                  <button
                      onClick={() => toggleQuestion(index)}
                      className="question-container--button"
                  >
                    <span className="question-container--text">{question.title}</span>
                  </button>
                  <img
                      onClick={() => toggleQuestion(index)}
                      src={icon}
                      alt=""
                      className={`arrow-icon ${
                          expandedQuestions.includes(index) ? 'arrow-up' : 'arrow-down'
                      }`}
                  />

                  <p
                      className="question-container--answer"
                      style={{
                        maxHeight: expandedQuestions.includes(index) ? '500px' : '0',
                        overflow: 'hidden',
                        transition: 'max-height 0.5s ease-in-out',
                      }}
                  >
                    {question.answer}
                  </p>
                </div>
            ))}
          </div>
        </div>
      </section>
  );
}

export default FAQs;



